.grid {
  display: grid;
  row-gap: 1rem;
  column-gap: 1rem;
  padding: 1rem;
  justify-items: center;
}

@media screen and (max-width: 1100px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1100px) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.lazy-image {
  display: block;
  height: auto;
  width: 100%;
}

.lazy-image.loaded:not(.has-error) {
   animation: loaded 400ms ease-in-out;
 }

.lazy-image.has-error {
   content: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVR42mP0d/GvBwADLwFjI/tmIwAAAABJRU5ErkJggg==');
 }

@keyframes loaded {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}
